import styled from "styled-components";

import EduMgmt from "../../assets/img/EduMgmt.png";

export const EduMgmtimg = styled.div`
  background: linear-gradient(
      90deg,
      rgba(0, 0, 0, 0.88) 20.87%,
      rgba(0, 0, 0, 0.132) 60.21%
    ),
    url(${EduMgmt});
  background-size: cover;
  background-position: center;
  height: 377px;
  margin-top: 100px;
  border-radius: 8px;
  display: flex;
  align-items: center;

  div {
    font-size: 18px;
    font-weight: 300;
    line-height: 1.8;
    margin-left: 50px;
    color: #fff;
    h2 {
      font-family: "NEXEN_TIRE_Bold";
      font-size: 36px;
      color: #fff;
      margin-bottom: 25px;
    }
  }
`;

export const Button = styled.button`
  background-color: ${(props) => (props.clicked ? "#FFFFFF" : "#1F65CE")};
  color: ${(props) => (props.clicked ? "#7F7F7F" : "#FFFFFF")};
  border: ${(props) => (props.clicked ? "2px solid #E4E4E4" : "none")};
  transition-property: background-color;
  transition-duration: 0.2s;
  font-size: 16px;
  font-weight: 700;
  border-radius: 8px;
  margin-top: 25px;
  margin-right: 25px;
  width: 180px;
  height: 60px;

  :hover {
    cursor: ${(props) => (props.clicked ? "pointer" : "default")};
    background-color: ${(props) => (props.clicked ? "#F3F3F3" : "#1F65CE")};
    border: ${(props) => (props.clicked ? "2px solid #E4E4E4" : "#1F65CE")};
    transition-property: background-color;
    transition-duration: 0.2s;
    :active {
      background-color: ${(props) => (props.clicked ? "#ececec" : "#1F65CE")};
    }
  }
`;
