import { Busines, Businesbox, BusinesText } from "./styled-compomemts";

import React from "react";
import Businesimg1 from "../../../../assets/img/Busines1.png";
import Businesimg2 from "../../../../assets/img/Busines2.png";
import Businesimg3 from "../../../../assets/img/Busines3.png";

function BusinesSarea() {
  return (
    <Busines>
      <h3>우리의 사업영역은 이렇습니다</h3>
      <Businesbox>
        <div className="Businesshop">
          <img src={Businesimg1} alt="" />
          <BusinesText>
            <h3>교육운영</h3>
            최고의 전문가 그룹 데이터분석, 인공지능 등 최고
            <br />의 전문가 그룹의 공공기관 훈련서비스
          </BusinesText>
        </div>
        <div className="Businesshop">
          <img src={Businesimg2} alt="" />
          <BusinesText>
            <h3>도서출판</h3>
            IT 출판 드림팀 교육용 교재로 채택되는 검증된
            <br />
            전문도서 파이썬으로 하는 데이터분석, 머신러닝,
            <br />
            딥러닝
          </BusinesText>
        </div>
        <div className="Businesshop">
          <img src={Businesimg3} alt="" />
          <BusinesText>
            <h3>스마트러닝</h3>
            지식콘텐츠 플랫품 혼합교육용 러닝사이트 온아이리포
            <br />
            데이터분석 인공지능 지식콘텐츠 디플와플
          </BusinesText>
        </div>
      </Businesbox>
    </Busines>
  );
}

export default BusinesSarea;
