import styled from "styled-components";

import { Nav, Navbar } from "react-bootstrap";

export const MeunNavbar = styled(Navbar)`

    .stick{
        position: fixed;
        top: 0;
        width: 100%;
        z-index: 999;
    }
`;

export const Menuimg = styled(Navbar.Brand)`
    img{
        width: 84px;
    }
`;

export const MenuNever = styled(Nav)`
    padding: 10px 0px;
    font-size: 16px;
    font-weight: 700;

    a{
        margin-left: 8vh;
        color: #000;

        &:hover{
            color: #1F65CE;
        }
    }
`