import styled from "styled-components";

export const Busines = styled.div`
  margin-top: 80px;

  h3 {
    font-size: 32px;
    font-weight: 700;
    color: #1f65ce;
  }
`;

export const Businesbox = styled.div`
  margin-top: 45px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  .Businesshop {
    width: auto;
    background-color: #fff;
    border: 2px solid #e4e4e4;
    border-radius: 8px;
    padding: 18px;
    img {
      border-radius: 8px;
    }
  }
`;

export const BusinesText = styled.div`
  margin-top: 20px;
  font-size: 14px;
  font-weight: 500;
  color: #535353;
  height: 130px;

  h3 {
    font-size: 18px;
    font-weight: bold;
    color: #000;
    margin-bottom: 18px;
  }
`;
