import styled from "styled-components";

import IntroductionImg1 from "../../assets/img/Introduction01.png";
import IntroductionImg2 from "../../assets/img/Introduction02.png";
import IntroductionImg3 from "../../assets/img/Introduction03.png";

export const ImageBox = styled.div`
  margin-top: 100px;
  display: flex;
  border-radius: 20px;
  background-size: cover;
  background-position: center;
  height: 450px;
  align-items: center;

  div {
    margin-left: auto;
    margin-right: 10vh;
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.8;
    h1 {
      margin-bottom: 2.5vh;
      font-size: 42px;
      font-weight: 900;
    }
  }

  img {
    width: 100%;
    border-radius: 20px;
  }
`;

export const ImageBox1 = styled(ImageBox)`
  background-image: url(${IntroductionImg1});
`;

export const ImageBox2 = styled(ImageBox)`
  background-image: url(${IntroductionImg2});
`;

export const ImageBox3 = styled(ImageBox)`
  background-image: url(${IntroductionImg3});

  div {
    color: #000;
  }
`;
