import styled, { css } from "styled-components";
import main from '../../assets/img/main.jpg'

export const MinImg = styled.div`
  background-image: url(${main});
  background-size: cover;
  background-position: center;
  height: 773px;
  display: flex;
  align-items: center;
`;

export const Maintitle = styled.div`
  div {
    font-size: 18px;
    font-weight: 300;
    color: #dfdfdf;
    margin-top: 30px;
    line-height: 1.8;
  }

  @font-face {
    font-family: "NEXEN_TIRE_Bold";
    src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_eight@1.0/NEXEN_TIRE_Bold.woff")
      format("woff");
    font-weight: normal;
    font-style: normal;
  }

  h1 {
    margin-bottom: 4vh;
    font-family: "NEXEN_TIRE_Bold";
    font-size: 48px;
    color: #ffffff;

    //PC 작업 완료이후 작업진행
    /* ${(props) =>
      props.isTabletOrMobile &&
      css`
        font-size: 325px;
      `} */
  }
`;
