import Maintext from "./locale/Maintext";

import { Maintitle, MinImg } from "./styled-components";
import { useMediaQuery } from "react-responsive";
import { Container } from "react-bootstrap";

// useMediaQuery Hook기능 부분은 PC 작업 완료이후 작업 진행
const Main = () => {
  const isTabletOrMobile = useMediaQuery({ query: "(max-width: 768px)" });

  return (
    <section>
      <MinImg>
        <Container>
          <Maintitle isTabletOrMobile={isTabletOrMobile}>
            <Maintext />
          </Maintitle>
        </Container>
      </MinImg>
    </section>
  );
};

export default Main;
