import {
  ServiceBackground,
  Widgetbox,
  EducationSection,
} from "./styled-components";

import { Container } from "react-bootstrap";

import icon1 from "../../assets/img/icon1.svg";
import icon2 from "../../assets/img/icon2.svg";
import icon3 from "../../assets/img/icon3.svg";
import icon4 from "../../assets/img/icon4.svg";

const ServiceOperation = () => {
  return (
    <ServiceBackground>
      <Container>
        <div className="Serviceh3">
          <h1>
            Service
            <br />
            Operation
          </h1>
        </div>
        <EducationSection>
          <Widgetbox>
            <img src={icon1} alt="" />
            <div>
              <h3>디플와플</h3>
              자체 온라인 강의사이트
              <br />
              디플와플 보유
            </div>
          </Widgetbox>
          <Widgetbox>
            <img src={icon2} alt="" />
            <div>
              <h3>공공기관 교육 콘텐츠</h3>
              공공기관 교육 콘텐츠
              <br />
              제작 및 납품
            </div>
          </Widgetbox>
          <Widgetbox>
            <img src={icon3} alt="" />
            <div>
              <h3>커리큘럼 제작 및 운영</h3>
              공공기관 빅데이터 교육
              <br />
              커리큘럼 제작 운영
            </div>
          </Widgetbox>
          <Widgetbox>
            <img src={icon4} alt="" />
            <div>
              <h3>수험서 및 교양 서적 제작</h3>
              IT/빅데이터 분야
              <br />
              수험서 및 교양 서적 제작
            </div>
          </Widgetbox>
        </EducationSection>
      </Container>
    </ServiceBackground>
  );
};

export default ServiceOperation;
