import React from "react";

import { ImageBox1, ImageBox2, ImageBox3 } from "./styled-components";
import { Container } from "react-bootstrap";

const Introduction = () => {
  return (
    <section>
      <Container>
        <ImageBox1>
          <div>
            <h1>아이리포는 고민합니다</h1>
            더 나은 미래를 위해 끊임 없이 이 시대에 필요한 지식
            <br />을 가장 효과적인 방법으로 전달하고자 늘 고민합니다.
          </div>
        </ImageBox1>
        <ImageBox2>
          <div>
            <h1>아이리포는 믿습니다</h1>
            훌륭한 지식 콘텐츠는 세상을 변화시킬 힘이 있습니다.
            <br />
            작은 곳에서전파된 지식이 점파 많은 사람에게 영감을
            <br />
            주고 영향력을 발휘하여세상을 바꾸는 큰 힘이 될 것 입니다
          </div>
        </ImageBox2>
        <ImageBox3>
          <div>
            <h1>아이리포는 성장합니다</h1>
            출판부터 교육서비스, 스마트러닝까지 최고의 지식 콘텐츠 <br />
            기업으로 우리는 계속해서 성장해 갈 것입니다.
          </div>
        </ImageBox3>
      </Container>
    </section>
  );
};

export default Introduction;
