import styled from "styled-components";

export const ServiceBackground = styled.section`
  background-color: #f5f5f5;
  margin-top: 95px;
  padding-bottom: 95px;

  .Serviceh3 {
    padding-top: 80px;
    h1 {
      line-height: 1.4;
      font-size: 52px;
      font-weight: 900;
      color: #1f65ce;
    }
  }
`;

export const EducationSection = styled.div`
  display: flex;
  justify-content: space-between;
  margin-top: 95px;
`;

export const Widgetbox = styled.div`
  width: 295px;
  height: 295px;
  padding-top: 45px;
  background-color: #fff;
  border-radius: 30px;
  box-shadow: 0px 0px 14px rgba(0, 0, 0, 0.25);
  align-items: center;
  text-align: center;
  border: 2px solid #fff;

  &:hover{
    border: 2px solid #1f65ce;
    cursor: pointer;
  }

  img{
    width: 85px;
    height: 85px;
  }

  div {
    padding: 25px;
    text-align: left;
    margin-top: 25px;
    font-size: 16px;
    color: #545454;

    h3 {
      font-size: 18px;
      font-weight: 700;
      margin-bottom: 15px;
      color: #000;
    }
  }
`;
