import styled from "styled-components";

import Historyimg from "../../assets/img/historyimg.png";

export const Historyimgbox = styled.div`
  background-image: url(${Historyimg});
  background-size: cover;
  background-position: center;
  height: 1762px;

  ::before {
    content: "";
    filter: blur(5px);
  }
`;

export const Historybox = styled.div`
  padding-top: 80px;
  h1 {
    font-size: 52px;
    font-weight: 900;
    color: #fff;
    line-height: 1.4;
  }
`;

export const Historyyear = styled.div`
  margin-top: 130px;
  display: flex;

  h2 {
    font-size: 52px;
    font-weight: 700;
    color: #fff;
  }
`;

export const Descriptionbox = styled.div`
  background-color: #fff;
  border-radius: 12px;
  padding: 24px;
  width: auto;
  margin: 0 6.5vh;

  div {
    display: flex;
    color: #595959;
    margin: 8px;
    align-items: start;
    font-size: 16px;
    font-weight: 500;

    h3 {
      color: #000;
      font-size: 18px;
      font-weight: 600;
      margin-bottom: 0;
    }
  }

  .blueball {
    width: 8px;
    height: 8px;
    border-radius: 99999px;
    background-color: #1f65ce;
  }
`;

export const Month = styled.div`
  padding: 8px 0 8px 0;
  .description {
    margin: 0;
    margin-left: 15px;
  }
`;
